<template>
    <v-container>    
        <v-layout>
            <v-flex>
                <v-data-table
                    :headers="headers"
                    :items="ventas"                    
                    sort-by="calories"
                    class="elevation-1"  
                    v-if="verNuevo==0"                  
                    >
                    <template v-slot:top>
                        <v-toolbar flat color="white">
                        <v-toolbar-title>Consulta Ventas</v-toolbar-title>
                        <v-divider
                            class="mx-4"
                            inset
                            vertical
                        ></v-divider>
                        <v-spacer></v-spacer>
                        Desde:&nbsp;                        
                        <v-text-field type="date" v-if="verNuevo==0" class="text-xs-center" v-model="fechaInicio"></v-text-field>
                        Hasta:&nbsp;                        
                        <v-text-field type="date" v-if="verNuevo==0" class="text-xs-center" v-model="fechaFin"></v-text-field>
                        <v-btn
                            v-if="verNuevo==0"
                            color="primary"
                            dark
                            class="mb-2"
                            @click="listar"                          
                        >Buscar</v-btn>                                   

                        <v-dialog v-model="comprobanteModal" max-width="1000px">
                            <v-card>
                                <v-car-text>
                                    <v-btn @click="crearPDF()"><v-icon>mdi-print</v-icon></v-btn>
                                    <div id="factura">
                                        <header>
                                            <div id="logo">
                                                <img src="data:image/png;base64," id="imagen">
                                            </div>
                                            <div id="datos">
                                                <p id="encabezado">
                                                    <b>IncanatoIT</b><br>José Gálvez 1368, Chongoyape - Chiclayo, Perú<br>Telefono:(+51)931742904<br>Email:jcarlos.ad7@gmail.com
                                                </p>
                                            </div>
                                            <div id="fact">
                                                <p>{{tipo_comprobante}}<br>
                                                {{serie_comprobante}}-{{num_comprobante}}<br>
                                                {{fecha_hora}}</p>
                                            </div>
                                        </header>
                                        <br>
                                        <section>
                                            <div>
                                                <table id="facliente">
                                                    <tbody>
                                                        <tr>
                                                            <td id="cliente">
                                                                <strong>{{cliente}}</strong><br>
                                                                <strong>Documento:</strong> {{num_documento}}<br>
                                                                <strong>Dirección:</strong> {{direccion}}<br>
                                                                <strong>Teléfono:</strong> {{telefono}}<br>
                                                                <strong>Email:</strong> {{email}}
                                                            </td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </section>
                                        <br>
                                        <section>
                                            <div>
                                                <table id="facarticulo">
                                                    <thead>
                                                        <tr id="fa">
                                                            <th>CANT</th>
                                                            <th>DESCRIPCION</th>
                                                            <th>PRECIO UNIT</th>
                                                            <th>DESC.</th>
                                                            <th>PRECIO TOTAL</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr v-for="det in detalles" :key="det.idventa">
                                                            <td style="text-align:center;">{{det.cantidad}}</td>
                                                            <td>{{det.articulo}}</td>
                                                            <td style="text-align:right;">{{det.precio.toFixed(2)}}</td>
                                                            <td style="text-align:right;">{{det.descuento.toFixed(2)}}</td>
                                                            <td style="text-align:right;">{{(det.cantidad*det.precio-det.descuento).toFixed(2)}}</td>
                                                        </tr>
                                                    </tbody>
                                                    <tfoot>
                                                        <tr>
                                                            <th></th>
                                                            <th></th>
                                                            <th></th>
                                                            <th style="text-align:right;">SUBTOTAL</th>
                                                            <th style="text-align:right;">L. {{totalParcial = (total-totalImpuesto).toFixed(2)}}</th>
                                                        </tr>
                                                        <tr>
                                                            <th></th>
                                                            <th></th>
                                                            <th></th>
                                                            <th style="text-align:right;">ISV({{impuesto}}%)</th>
                                                            <th style="text-align:right;">L. {{totalImpuesto=((total*impuesto)/(100+impuesto)).toFixed(2)}}</th>
                                                        </tr>
                                                        <tr>
                                                            <th></th>
                                                            <th></th>
                                                            <th></th>
                                                            <th style="text-align:right;">TOTAL</th>
                                                            <th style="text-align:right;">L. {{total=(calcularTotal).toFixed(2)}}</th>
                                                        </tr>
                                                    </tfoot>
                                                </table>
                                            </div>
                                        </section>
                                        <br>
                                        <br>
                                        <footer>
                                            <div id="gracias">
                                                <p><b>Gracias por su compra!</b></p>
                                            </div>
                                        </footer>
                                    </div>
                                    <v-btn @click="ocultarComprobante" color="blue darken-1" text>Cancelar</v-btn>
                                </v-car-text>
                            </v-card>
                        </v-dialog>

                        </v-toolbar>
                    </template>
                    <template v-slot:item.opciones="{ item }">
                        <v-icon
                        small
                        class="mr-2"
                        @click="verDetalles(item)"
                        >
                        mdi-tab
                        </v-icon>
                        <v-icon
                        small
                        class="mr-2"
                        @click="mostrarComprobante(item)"
                        >
                        mdi-table
                        </v-icon>                                             
                        
                    </template >

                    <template class="justify-center" v-slot:item.estado="{ item }">
                        <div v-if="item.estado == 'Aceptado'">                            
                            <v-chip color="green" dark>Aceptado</v-chip>
                        </div>
                        <div v-else>
                            <v-chip color="red" dark>{{item.estado}}</v-chip>
                        </div>
                    </template>

                    <template v-slot:no-data>
                        <v-btn color="primary" @click="listar">Resetear</v-btn>
                    </template>
                    </v-data-table>

                    <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
                        <v-layout row wrap>
                            <v-flex xs12 ms4 md4 lg4 xl4>
                                <v-select v-model="tipo_comprobante"
                                    :items="comprobantes" label="TipoComprobante">
                                </v-select>
                            </v-flex>

                            <v-flex xs12 ms4 md4 lg4 xl4>
                                <v-text-field v-model="serie_comprobante" label="Serie Comprobante">
                                </v-text-field>
                            </v-flex>

                            <v-flex xs12 ms4 md4 lg4 xl4>
                                <v-text-field v-model="num_comprobante" label="Numero Comprobante">
                                </v-text-field>
                            </v-flex>

                            <v-flex xs12 ms4 md4 lg4 xl4>
                                <v-select v-model="idcliente"
                                    :items="clientes" label="Cliente">
                                </v-select>
                            </v-flex>

                            <v-flex xs12 ms4 md4 lg4 xl4>
                                <v-text-field type="number" v-model="impuesto" label="Impuesto">
                                </v-text-field>
                            </v-flex>
                            


                            <v-flex xs12 ms8 md8 lg8 xl8>
                                <v-text-field @keyup.enter="buscarCodigo()" v-model="codigo" label="Codigo">
                                </v-text-field>
                            </v-flex>

                            <v-flex xs12 ms2 md2 lg2 xl2>
                                <v-btn @click="mostrarArticulos()" small fab dark color="teal">
                                    <v-icon class="material-icons" dark>mdi-plus</v-icon>
                                </v-btn>
                            </v-flex>

                            <v-flex xs12 ms2 md2 lg2 xl2 v-if="errorArticulo">
                                <div class="red--text" v-text="errorArticulo"></div>
                            </v-flex>

                            <v-flex xs12 ms12 md12 lg12 xl12>
                                <v-data-table
                                    :headers="cabeceraDetalles"
                                    :items="detalles"
                                    hide-actions                                    
                                    class="elevation-1"                    
                                    >
                                    <template v-slot:top>
                                        <v-toolbar flat color="white">
                                        <v-toolbar-title>Detalles</v-toolbar-title>
                                        <v-divider
                                            class="mx-4"
                                            inset
                                            vertical
                                        ></v-divider>
                                        <v-spacer></v-spacer>
                                        <v-text-field class="text-xs-center" v-model="search" append-icon="mdi-search" label="Busqueda" single-line hide-details></v-text-field>
                                        <v-spacer></v-spacer>
                                        <v-dialog v-model="dialog" max-width="500px">
                                            <template v-slot:activator="{ on, attrs }">
                                            <v-btn                                            
                                                color="primary"
                                                dark
                                                class="mb-2"
                                                v-bind="attrs"
                                                v-on="on"
                                            >Nuevo</v-btn>
                                            </template>
                                            <v-card>                                                                        
                                            
                                
                                            <v-card-actions>
                                                <v-spacer></v-spacer>
                                                <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                                                <v-btn color="blue darken-1" text @click="guardar">Guardar</v-btn>
                                            </v-card-actions>
                                            </v-card>
                                        </v-dialog>

                                        
                                        </v-toolbar>
                                    </template>                                    
                                    
                                    <template v-slot:item.cantidad="{ item }">
                                        <v-text-field type="number" v-model="item.cantidad"></v-text-field>
                                    </template >

                                    <template v-slot:item.precio="{ item }">
                                        <v-text-field type="number" v-model="item.precio"></v-text-field>
                                    </template >

                                    <template v-slot:item.descuento="{ item }">
                                        <v-text-field type="number" v-model="item.descuento"></v-text-field>
                                    </template >

                                    <template v-slot:item.subtotal="{ item }">
                                       L.{{item.cantidad * item.precio - item.descuento}}                                        
                                    </template >

                                    <template v-slot:no-data>
                                        <v-btn color="primary" @click="listar">Resetear</v-btn>
                                    </template>
                                    
                                    <template slot="no-data">
                                        No hay articulos agregados al detalle.
                                    </template>

                                    </v-data-table>
                                    <v-flex class="text-sm-right">
                                        <strong>Total Parcial: </strong>L.{{totalParcial=(total-totalImpuesto).toFixed(2)}}
                                    </v-flex>
                                    <v-flex class="text-sm-right">
                                        <strong>Total Impuesto: </strong>L.{{totalImpuesto=(total*impuesto/(100+impuesto)).toFixed(2)}}
                                    </v-flex>
                                    <v-flex class="text-sm-right">
                                    <strong>Total Neto: </strong>L.{{total=(calcularTotal).toFixed(2)}}
                                    </v-flex>
                            </v-flex>

                            <v-flex xs12 ms12 md12 lg12 xl12>
                                <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                            </v-flex>

                            <v-flex xs12 ms12 md12 lg12 xl12>
                                <v-btn @click="ocultarNuevo()" color="blue darken-1" text>Cancelar</v-btn>
                                <v-btn v-if="verDet==0" @click="guardar()" color="success">Guardar</v-btn>
                            </v-flex>                           

                        </v-layout>
                    </v-container>
            </v-flex>
        </v-layout>
    </v-container>
</template>
<script>
    import axios from 'axios'
    import jsPDF from 'jspdf'
    import html2canvas from 'html2canvas'
    export default {
        data(){
            return {
                ventas:[],
                dialog: false,
                headers: [
                    { text: 'Opciones', value: 'opciones', sortable: false },                
                    { text: 'Usuario', value: 'usuario' },
                    { text: 'Cliente', value: 'cliente' },                
                    { text: 'Tipo Comprobante', value: 'tipo_comprobante' },                
                    { text: 'Serie Comprobante', value: 'serie_comprobante', sortable: false },                
                    { text: 'Numero Comprobante', value: 'num_comprobante',  sortable: false },
                    { text: 'Fecha', value: 'fecha_hora', sortable: false },
                    { text: 'Impuesto', value: 'impuesto', sortable: false },                
                    { text: 'Total', value: 'total', sortable: false },                
                    { text: 'Estado', value: 'estado', sortable: false },                               
                ],
                cabeceraDetalles: [                    
                    { text: 'Articulo', value: 'articulo', sortable: false },                
                    { text: 'Cantidad', value: 'cantidad',  sortable: false },
                    { text: 'Precio', value: 'precio', sortable: false },
                    { text: 'Descuento', value: 'descuento', sortable: false },
                    { text: 'Subtotal', value: 'subtotal', sortable: false },                                                    
                ],
                detalles:[                                       
                ],
                search:'',                
                id:'',
                idcliente:'',
                clientes:[],
                tipo_comprobante:'',
                comprobantes:['FACTURA','COMPROBANTE','RECIBO', 'ORDEN'],
                serie_comprobante:'',
                num_comprobante:'',
                impuesto: 15,
                codigo:'',
                verNuevo:0,
                errorArticulo:null,
                totalParcial:0,
                totalImpuesto:0,
                total:0,
                cabeceraArticulos:[
                    {text:'Seleccionar', value:'seleccionar', sortable:false},
                    {text:'Articulo', value:'nombre'},
                    {text:'Categoria', value:'categoria'},
                    {text:'Descripcion', value:'descripcion', sortable:false},
                    {text:'Stock', value:'stock', sortable:false},
                    {text:'Precio Venta', value:'precio_venta', sortable:false}
                ],
                articulos:[],
                texto:'',
                verArticulos: 0,
                verDet:0,
                valida:0,
                validaMensaje:[],
                adModal: 0,
                adAccion:0,
                adNombre:'',
                adId: '',
                comprobanteModal:0,
                cliente:'',
                fecha_hora:'',
                num_documento:'',
                direccion:'',
                email:'',
                telefono:'',
                fechaInicio:'',
                fechaFin:''
            }
        },

        computed: {
            calcularTotal:function(){
                var resultado = 0.0;
                for(var i=0; i<this.detalles.length;i++){
                    resultado = resultado + (this.detalles[i].precio*this.detalles[i].cantidad-this.detalles[i].descuento);
                }
                return resultado;
            }
        },

        watch: {
            dialog (val) {
            val || this.close()
            },
        },

        created () {            
            this.listar();
            this.select();
        },

        methods:{

            crearPDF(){
                var quotes = document.getElementById('factura');
                html2canvas(quotes).then(function(canvas){
                    var imgData = canvas.toDataURL('image/png');
                    var imgWidth = 210;
                    var pageHeight = 295;
                    var imgHeigth = canvas.height * imgWidth / canvas.width;
                    var heightLEft = imgHeigth;
                    var doc = new jsPDF();
                    var position = 0;

                    doc.addImage(imgData,'PNG',0,position,imgWidth,imgHeigth);
                    doc.save('Comprobante venta.pdf')
                });
            },

            mostrarComprobante(item){
                this.limpiar();
                this.tipo_comprobante = item.tipo_comprobante;
                this.serie_comprobante = item.serie_comprobante;
                this.num_comprobante = item.num_comprobante;
                this.cliente =item.cliente;
                this.num_documento = item.num_documento;
                this.direccion = item.direccion;
                this.telefono = item.telefono;
                this.email = item.email;
                this.fecha_hora = item.fecha_hora;
                this.impuesto = item.impuesto;
                this.listarDetalles(item.idventa);                
                this.comprobanteModal = 1;
            },
            ocultarComprobante(){
                this.comprobanteModal = 0; 
                this.limpiar();               
            },

            mostrarNuevo(){
                this.verNuevo = 1;
            },
            ocultarNuevo(){
                this.verNuevo = 0;
                this.limpiar();
            },            

            agregarDetalle(data = []){
                if(this.encuentra(data['idarticulo'])){
                    this.errorArticulo = "El articulo ya ha sido agregado."
                }
                else{
                    this.detalles.push(
                    {idarticulo:data['idarticulo'],
                    articulo: data['nombre'],
                    cantidad:1,
                    precio:data['precio_venta'],
                    descuento:0}
                    )
                }
                
            },

            encuentra(id){
                var sw=0;
                for(var i = 0;  i<this.detalles.length; i++){
                    if (this.detalles[i].idarticulo == id){
                        sw=1;
                    }
                }
                return sw;
            },

            

            listar(){
                let me = this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers: header};
                let url = '';
                if(!me.fechaInicio || !me.fechaFin){
                    url = 'api/Ventas/Listar'
                }
                else{
                    url = 'api/Ventas/ConsultaFechas/'+me.fechaInicio+'/'+me.fechaFin;
                }
                axios.get(url,configuracion).then(function(response){
                    me.ventas = response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },

            listarDetalles(id){
                let me = this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers: header};
                axios.get('api/Ventas/ListarDetalles/'+id,configuracion).then(function(response){
                    me.detalles = response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },

            verDetalles(item){
                this.limpiar();
                this.tipo_comprobante = item.tipo_comprobante;
                this.serie_comprobante = item.serie_comprobante;
                this.num_comprobante = item.num_comprobante;
                this.idcliente = item.idcliente;
                this.impuesto = item.impuesto;
                this.listarDetalles(item.idventa);
                this.verNuevo=1;    
                this.verDet=1;
            },


            select(){
                let me = this;
                var clientesArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers: header};
                axios.get('api/Personas/SelectClientes',configuracion).then(function(response){                    
                    clientesArray = response.data;  
                    clientesArray.map(function(x){
                        me.clientes.push({text:x.nombre, value:x.idpersona});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            

            deleteItem (item) {
                const index = this.desserts.indexOf(item)
                confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
            },
           

            limpiar(){
                this.id="",
                this.idcliente="",                
                this.nombre="",
                this.tipo_comprobante="",
                this.serie_comprobante="",
                this.num_comprobante="",
                this.impuesto=15,
                this.codigo="",
                this.detalles= [],
                this.total=0,
                this.totalImpuesto=0,
                this.totalParcial=0;  
                this.verDet=0;              
            },         
            

        }
    }
</script>
<style>
    #factura {
            padding: 20px;
            font-family: Arial, sans-serif;
            font-size: 16px ;
        }

        #logo {
            float: left;
            margin-left: 2%;
            margin-right: 2%;
        }
        #imagen {
            width: 100px;
        }

        #fact {
            font-size: 18px;
            font-weight: bold;
            text-align: center;
        }   

        #datos {
            float: left;
            margin-top: 0%;
            margin-left: 2%;
            margin-right: 2%;
            /*text-align: justify;*/
        }

        #encabezado {
            text-align: center;
            margin-left: 10px;
            margin-right: 10px;
            font-size: 16px;
        }

        section {
            clear: left;
        }

        #cliente {
            text-align: left;
        }

        #facliente {
            width: 40%;
            border-collapse: collapse;
            border-spacing: 0;
            margin-bottom: 15px;
        }

        #fa {
            color: #FFFFFF;
            font-size: 14px;
        }

        #facarticulo {
            width: 100%;
            border-collapse: collapse;
            border-spacing: 0;
            padding: 20px;
            margin-bottom: 15px;
        }

        #facarticulo thead {
            padding: 20px;
            background: #2183E3;
            text-align: center;
            border-bottom: 1px solid #FFFFFF;
        }

        #gracias {
            text-align: center;
        }
</style>

